.inspector_closebutton:hover::before, .inspector_closebutton:hover::after {
      background-color: black;
    }
.inspector_closebutton:before, .inspector_closebutton:after {
      content: '';
      width: 2rem;
      height: 2px;
      background-color: #707070;
      -webkit-transform-origin: center;
              transform-origin: center;
      position: absolute;
      top: calc(1rem - 1px);
      right: 0;
    }
.inspector_closebutton:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
.inspector_closebutton:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
    }
:global .sideborder span {
    background-clip: padding-box;
    padding: 0;
    box-sizing: border-box;
    white-space: pre;
    position: relative;
    display: inline-block;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-width: 0 1px;
    background: repeating-linear-gradient(-45deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06) 6px, rgba(0, 0, 0, 0.09) 6px, rgba(0, 0, 0, 0.09) 12px);
  }
:global .copied:hover:after {
    -webkit-animation: wiggle 0.5s normal forwards ease;
            animation: wiggle 0.5s normal forwards ease;
    opacity: 1;
  }
:global .pseudocopybutton:active, :global .pseudocopybutton:focus {
    transition: 0.3s all ease;
    opacity: 0.5;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
:global .pseudocopybutton:active::after, :global .pseudocopybutton:focus::after {
      -webkit-transform: translateY(calc(-50% + 0.125rem));
              transform: translateY(calc(-50% + 0.125rem));
    }
